<template>
  <div class='cashWithdrawal'>
    <div>
      <CommonHeader></CommonHeader>
    </div>
    <!--tab导航部分-->
    <div class="fix">
      <Set></Set>
    </div>
    <!--内容区域-->
    <div class='cashWithdrawal-content'>
      <!--头部-->
      <div class='content-header'>
        <div class='c-header'>
          <button type="button" class="btn btn-success pull-right" @click='oneKeySuccess' v-if='btnShow'>批量支付</button>
          <div class='c-h-Text'>提现打款</div>
          <span class='border-cover'></span>
        </div>
      </div>
      <!--头部-->
      <!--查询条件-->
      <div class='content-header-top'>
        <div class='datd-range'>
          <!--element日历控件-->
          <span class="demonstration">日期范围：</span>
          <el-select v-model="time" placeholder="日期范围" @change='chooseTime' style='width:120px;margin-left:8px;'>
            <el-option value="1" label="近7天"></el-option>
            <el-option value="2" label="近一个月"></el-option>
            <el-option value="3" label="近两个月"></el-option>
            <el-option value="4" label="近三个月"></el-option>
          </el-select>
          <el-date-picker :editable="editable" type="date" v-model='cashCondition.startDate' @change='dateOne' placeholder="选择日期">
          </el-date-picker>
          <span class="demonstration">-</span>
          <el-date-picker :editable="editable" v-model='cashCondition.endDate' type="date" @change='dateTwo' placeholder="选择日期">
          </el-date-picker>
          <!--element日历控件-->
        </div>
        <div class='query-criteria'>
          <span class="demonstration">查询条件：</span>
          <el-select v-model="cashCondition.user" style='width:120px;margin-left:8px;'>
            <el-option :value="2" label="协诊专家"></el-option>
            <el-option :value="1" label="基层医生"></el-option>
            <el-option :value="''" label="全部申请人"></el-option>
          </el-select>
          <input type="text" placeholder='医生/专家姓名/手机号' v-model='cashCondition.search' class="form-control P-t-inputBox3" style='width:200px;display:inline-block'>
          <button style='margin-left:10px;' type="button" class='btn btn-primary' @click='search'>查询</button>
        </div>
      </div>
      <!--查询条件-->
      <!--导航-->
      <div class='navigation'>
        <!--导航-->
        <nav class="nav boder4" style="margin-top:20px; ">
          <ul class="nav nav-tabs" role="tablist">
            <li class='active' @click='newCaseShow1' role="presentation">
              <a href="#home" aria-controls="home" data-toggle="tab">待付款（{{statusObj.waitTotal}}）</a>
            </li>
            <li @click='newCaseShow2'>
              <a href="#profile" aria-controls="profile" data-toggle="tab">付款成功（{{statusObj.successTotal}}）</a>
            </li>
            <li @click='newCaseShow3'>
              <a href="#messages" aria-controls="messages" data-toggle="tab">付款失败（{{statusObj.failTotal}}）</a>
            </li>
          </ul>
        </nav>
        <!--导航-->
      </div>
      <!--导航-->
      <!--表格-->
      <div class='main-content'>
        <table class="table table-bordered">
          <thead>
            <tr>
              <th v-if='btnShow'>
                <input type="checkbox" v-model='Allbatch' @change='AllbatchChange'>
              </th>
              <th>申请单号</th>
              <th>申请人</th>
              <th>手机号</th>
              <th>申请人类型</th>
              <th>申请金额</th>
              <th>申请时间</th>
              <th>操作时间</th>
              <th>操作人</th>
              <th>操作</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for='(item,index) in cashList'>
              <td v-if='btnShow'>
                <input type="checkbox" v-model='batchArr[index]' @change='changeBatchAll'>
              </td>
              <td>{{item.withdrawNo}}</td>
              <td>{{item.userName}}</td>
              <td>{{item.mobile}}</td>
              <td>{{item.userType}}</td>
              <td>{{item.money}}</td>
              <td>{{item.createdWhen | dataTimeFilter}}</td>
              <td>{{item.updatedWhen | dataTimeFilter}}</td>
              <td>{{item.updatedName}}</td>
              <td>
                <!--<router-link :to='"/PatientDetails/"+item.id'>-->
                <button type="button" class="btn btn-primary btn-sm details-btn" @click='orderDetails(item.id)'>
                  <i class="fa fa-clone" aria-hidden="true"></i> 订单详情</button>
                <!--</router-link>-->
                <button type="button" class="btn btn-success" v-if='btnShow' @click='paymentSuccess(item.status,item.bankCardId,item.id,item.usersId)'>
                  付款成功
                </button>
                <button type="button" class="btn btn-warning" v-if='btnShow' @click='paymentWarning(item.status,item.bankCardId,item.id,item.usersId)'>
                  付款失败
                </button>
                <!--成功模态框-->
                <div class='success-model'>
                  <el-dialog title="付款提示" :visible.sync="dialogVisible" size="tiny">
                    <div class='model-body1'>
                      <span class='circle'>!</span>
                      <span class='text'>选中的订单付款成功请确认</span>
                    </div>
                    <span slot="footer" class="dialog-footer">
                      <el-button @click="dialogVisible = false">取 消</el-button>
                      <el-button type="primary" @click='successComfirm(item.status,item.bankCardId,item.id,item.usersId)'>确 定</el-button>
                    </span>
                  </el-dialog>
                </div>
                <!--成功模态框-->
                <!--失敗模态框-->
                <div class='warning-model'>
                  <el-dialog title="付款提示" :visible.sync="dialogVisible1" size="tiny">
                    <div style='font-size:18px'>
                      <el-radio class="radio" v-model="failReason" label="账号、户名不符">账号、户名不符</el-radio>
                      <el-radio class="radio" v-model="failReason" label="开户支行、开户地不符">开户支行、开户地不符</el-radio>
                      <el-radio class="radio" v-model="failReason" label="其他原因">其他原因</el-radio>
                    </div>
                    <span slot="footer" class="dialog-footer">
                      <el-button @click="dialogVisible1 = false">取 消</el-button>
                      <el-button type="primary" @click='failComfirm(item.status,item.bankCardId,item.id,item.usersId)'>确 定</el-button>
                    </span>
                  </el-dialog>
                </div>
                <!--失敗模态框-->
              </td>
            </tr>
            <tr v-if='noMesShow4'>
              <td colspan="10">
                暂无数据
              </td>
            </tr>
          </tbody>
        </table>

      </div>
      <!--表格-->
      <!--翻页组件-->
      <flip @customEvent='customEvent' :Body='cashBody' :Condition='cashCondition'></flip>
      <!--翻页组件-->
    </div>
  </div>
</template>
<script> 
//header
import CommonHeader from '../public/header.vue';
//menu
import Set from '../public/set.vue';
import moment from 'moment';
import flip from '../public/flip.vue'//翻页组件
export default {
  name: 'CashWithdrawal',
  data() {
    return {
      editable: false,
      time: '',
      type: 1,
      failReason: '账号、户名不符',
      cashCondition: {
        status: 2,
        search: '',
        dir: '',
        startDate: '',
        endDate: '',
        pageNumber: 1,
        pageSize: 10,
        user: '',
        orderBy: 'created_when'
      },
      statusCashCondition: {},
      Authorization: '',
      cashList: [],
      noMesShow4: false,
      cashBody: {},
      orderDetail: {
        "bankCardId": 0,
        "description": "",
        "id": 0,
        "status": 0,
        "usersId": 0
      },
      btnShow: true,
      totalAll: 0,//页数
      batchArr: [],//批量操作的数组
      Allbatch: false,//控制全部的变量
      idAggregate: {
        "ids": [],
        "status": 3,
        "userIds": []
      },//批量对象
      statusObj: {},//状态数量
      dialogVisible: false,
      dialogVisible1: false,
      locationBox: {}//打款参数
    }
  },
  mounted() {
    this.geCashList();
    this.getStatus()

  },
  methods: {
    newCaseShow1() {
      this.cashCondition.pageNumber = 1
      this.cashCondition.status = 2;
      this.cashCondition.orderBy = 'created_when'
      this.cashCondition.dir = ''
      this.btnShow = true
      this.geCashList();
    },
    newCaseShow2() {
      this.cashCondition.pageNumber = 1
      this.cashCondition.status = 3;
      this.cashCondition.orderBy = 'updated_when'
      this.cashCondition.dir = 'desc'
      this.btnShow = false
      this.geCashList();
    },
    newCaseShow3() {
      this.cashCondition.pageNumber = 1
      this.cashCondition.status = 4;
      this.cashCondition.orderBy = 'updated_when'
      this.cashCondition.dir = 'desc'
      this.btnShow = false
      this.geCashList();
    },
    // 打款成功
    successComfirm(status, bankCardId, id, usersId) {
      this.orderDetail = {
        "bankCardId": this.locationBox.bankCardId,
        "description": "",
        "id": this.locationBox.id,
        "status": 3,
        "usersId": this.locationBox.usersId
      }
      var url = '/api/assist-order/orderDetail/withdrawCash';
      this.Authorization = localStorage.getItem('access_token')
      this.$http.put(url, this.orderDetail, {
        headers: {
          Authorization: 'bearer ' + this.Authorization
        }
      }).then(function(res) {
        this.geCashList()
        this.getStatus()
        this.dialogVisible = false
        this.$message({
          message: '操作成功',
          type: 'success'
        });
      }, function() {
        this.$message({
          message: '操作失败',
          type: 'warning'
        });
      })

    },
    // 打款失败
    failComfirm() {
      this.orderDetail = {
        "bankCardId": this.locationBox.bankCardId,
        "description": this.failReason,
        "id": this.locationBox.id,
        "status": 4,
        "usersId": this.locationBox.usersId
      }
      var url = '/api/assist-order/orderDetail/withdrawCash';
      this.Authorization = localStorage.getItem('access_token')
      this.$http.put(url, this.orderDetail, {
        headers: {
          Authorization: 'bearer ' + this.Authorization
        }
      }).then(function(res) {
        this.geCashList()
        this.getStatus()
        this.dialogVisible1 = false
        this.$message({
          message: '操作成功',
          type: 'success'
        });
      }, function() {
        this.$message({
          message: '操作失败',
          type: 'warning'
        });
      })

    },
    // 订单详情
    orderDetails(id) {
      this.$router.push({ name: 'CashOrderDetails', query: { id: id } })
    },
    // 选择时间
    chooseTime() {
      if (this.time == 1) {//本周
        var endDate = new Date();
        var startDate = endDate - 1000 * 60 * 60 * 24 * 7;
        endDate = moment(endDate).format('YYYY-MM-DD')
        startDate = moment(startDate).format('YYYY-MM-DD')
        this.cashCondition.startDate = startDate
        this.cashCondition.endDate = endDate
      }
      if (this.time == 2) {//本月
        var endDate = new Date();
        var startDate = endDate - 1000 * 60 * 60 * 24 * 30;
        endDate = moment(endDate).format('YYYY-MM-DD')
        startDate = moment(startDate).format('YYYY-MM-DD')
        this.cashCondition.startDate = startDate
        this.cashCondition.endDate = endDate
      }
      if (this.time == 3) {//最近三个月
        var endDate = new Date();
        var startDate = endDate - 1000 * 60 * 60 * 24 * 60;
        endDate = moment(endDate).format('YYYY-MM-DD')
        startDate = moment(startDate).format('YYYY-MM-DD')
        this.cashCondition.startDate = startDate
        this.cashCondition.endDate = endDate
      }
      if (this.time == 4) {//三个月
        var endDate = new Date();
        var startDate = endDate - 1000 * 60 * 60 * 24 * 90;
        endDate = moment(endDate).format('YYYY-MM-DD')
        startDate = moment(startDate).format('YYYY-MM-DD')
        this.cashCondition.startDate = startDate
        this.cashCondition.endDate = endDate
      }
    },
    // 获取提现列表
    geCashList() {
      if (this.cashCondition.startDate != '') this.cashCondition.startDate = moment(this.cashCondition.startDate).format('YYYY-MM-DD') + ' 00:00:00';
      if (this.cashCondition.endDate != '') this.cashCondition.endDate = moment(this.cashCondition.endDate).format('YYYY-MM-DD') + ' 23:59:59';
      var url = '/api/assist-order/orderDetail/withdrawCash'
      this.Authorization = localStorage.getItem('access_token');
      this.$http.get(url, {
        headers: {
          Authorization: 'bearer ' + this.Authorization
        },
        params: this.cashCondition
      }).then(function(res) {
        this.batchArr = []
        this.cashList = res.body.list
        if (this.cashList.length == 0) {
          this.noMesShow4 = true;
        } else if (this.cashList.length > 0) {
          this.noMesShow4 = false
        }
        this.cashBody = res.body
        this.totalAll = res.body.lastPage
        for (var i = 0; i < this.cashList.length; i++) {
          this.batchArr.push(false);
        }
      })
    },
    // 搜索
    search() {
      this.cashCondition.pageNumber = 1
      this.geCashList();
      this.getStatus()
    },
    // 控制全部的checkbox
    AllbatchChange() {
      if (this.Allbatch) {
        for (var i = 0; i < this.batchArr.length; i++) {
          this.batchArr[i] = true;
        }
      } else {
        for (var i = 0; i < this.batchArr.length; i++) {
          this.batchArr[i] = false;
        }
      }
      this.batchArr = this.batchArr.concat()

    },
    // 监控全局checkbox的状态
    changeBatchAll() {
      var arr = 0;
      for (var i = 0; i < this.batchArr.length; i++) {
        if (this.batchArr[i]) {
          arr++
        }
      }
      if (arr == this.batchArr.length) {
        this.Allbatch = true;
      } else {
        this.Allbatch = false;
      }
    },
    // // 批量打款成功
    oneKeySuccess() {
      var arr = 0;
      for (var i = 0; i < this.batchArr.length; i++) {
        if (!this.batchArr[i]) {
          arr++
        }
      }
      if (this.batchArr.length == 0) {
        this.$message({
          message: '对不起，没有数据',
          type: 'warning'
        });
        return
      } else if (arr == this.batchArr.length) {
        this.$message({
          message: '请勾选打款对象',
          type: 'warning'
        });
      } else {
        for (var i = 0; i < this.batchArr.length; i++) {
          if (this.batchArr[i]) {
            this.idAggregate.ids.push(this.cashList[i].id)
            this.idAggregate.userIds.push(this.cashList[i].usersId)
          }
        }
        var url = '/api/assist-order/orderDetail/withdrawCash/ids'
        this.$http.put(url, this.idAggregate, {
          headers: {
            Authorization: 'bearer ' + this.Authorization
          }
        }).then(res => {
          this.idAggregate = {
            "ids": [],
            "status": 3,
            "userIds": []
          }
          this.geCashList();
          this.getStatus()
          this.$message({
            message: '批量操作成功',
            type: 'success'
          });
        }, err => {
          this.$message({
            message: '批量操作失败',
            type: 'warning'
          });
        })
      }

    },
    // 状态的数量统计
    getStatus() {
      this.statusCashCondition = {
        status: '',
        search: this.cashCondition.search,
        dir: this.cashCondition.dir,
        startDate: this.cashCondition.startDate,
        endDate: this.cashCondition.endDate,
        user: this.cashCondition.user
      }
      var url = '/api/assist-order/orderDetail/withdrawCash/num';
      this.Authorization = localStorage.getItem('access_token');
      this.$http.get(url, {
        headers: {
          Authorization: this.Authorization
        },
        params: this.statusCashCondition
      }).then((res) => {
        this.statusObj = res.body
      })
    },
    // 解决前大后
    dateOne() {
      if (new Date(this.cashCondition.startDate) >= new Date(this.cashCondition.endDate)) {
        var t = new Date(this.cashCondition.startDate) + 1000 * 60 * 60 * 24
        this.cashCondition.endDate = moment(t).format('YYYY-MM-DD')
      }
    },
    // 解决前大后        
    dateTwo() {
      if (new Date(this.cashCondition.startDate) >= new Date(this.cashCondition.endDate)) {
        var t = new Date(this.cashCondition.startDate) + 1000 * 60 * 60 * 24
        this.cashCondition.endDate = moment(t).format('YYYY-MM-DD')
      }
    },
    paymentSuccess(status, bankCardId, id, usersId) {
      this.locationBox = {
        status: status,
        bankCardId: bankCardId,
        id: id,
        usersId: usersId
      }
      this.dialogVisible = true
    },
    paymentWarning(status, bankCardId, id, usersId) {
      this.locationBox = {
        status: status,
        bankCardId: bankCardId,
        id: id,
        usersId: usersId
      }
      this.dialogVisible1 = true
    },
    // 翻页事件
    customEvent(cashCondition) {
      this.cashCondition=cashCondition;
      this.geCashList();
    }
  },
  components: {
    CommonHeader: CommonHeader,
    Set: Set,
    flip:flip
  }
}
</script>
<style scoped>
/*大局*/

.cashWithdrawal-content {
  margin: 50px 15px 0px 195px;
  min-width: 960px;
}






/*头部*/

.cashWithdrawal-content .content-header .c-header {
  padding: 8px 0px;
  font-size: 14px;
  height: 50px;
  background: white;
  position: relative;
  border-bottom: 1px solid #ddd;
}

.c-h-Text {
  padding: 8px 8px 8px 8px;
}

.c-header .border-cover {
  display: block;
  width: 1px;
  height: 14px;
  background: #6886d4;
  position: absolute;
  left: 1px;
  top: 17px;
}

select {
  height: 34px;
  width: 100px;
  border: solid 1px #ccc;
  border-radius: 4px;
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075);
}

.content-header-top {
  margin-top: 15px;
  margin-bottom: 15px;
}

.content-header-top .query-criteria {
  margin-top: 15px;
}









/*表格样式*/

.main-content {
  margin-top: 30px;
}

.main-content table th,
.main-content table {
  text-align: center;
}

.main-content .table thead {
  background: #f5f5f5;
}

.main-content.table tr {
  text-align: center;
}

.main-content .table td {
  padding: 4px 0px;
  line-height: 39px;
}

.main-content .table th {
  text-align: center;
  border-bottom: none;
}



/*模态框样式*/

.success-model .circle {
  display: inline-block;
  height: 40px;
  width: 40px;
  background: #f7ba2a;
  border-radius: 50%;
  line-height: 40px;
  font-size: 30px;
  text-align: center;
  color: white;
}

.success-model .text {
  display: inline-block;
  font-size: 16px;
}
</style>


